import Modal from "./segments/modal";
import {removeParameterFromUrl} from "../helpers/url"
import Container from './segments/container';
import ToggleButton from './segments/toggle-button';
import {saveSegment} from "./segments/ajax";
import Instructions from "./segments/instructions";

export default class Segments {

	constructor( table, search, sorting ) {
		// Dependencies
		this.Table = table;
		this.Search = search;
		this.Sorting = sorting;

		this.ToggleButton = new ToggleButton( document.querySelector( '.ac-button__segments' ) );
		this.Container = new Container( document.querySelector( '.ac-segments' ) );
		this.Modal = new Modal( document.querySelector( '#ac-modal-create-segment' ), this );

		// Variables
		this.segments = new Map();

		// Initiation
		AdminColumns.Modals.register( this.Modal, 'create_segment' );

		this.initEvents();
	}

	getCurrentState() {
		return {
			rules : JSON.stringify( this.Search.getRules() ),
			sorting : JSON.stringify( this.Sorting )
		}
	}

	unsetCurrent() {
		let url = window.location.href;

		this.Container.setCurrent( '' );
		history.replaceState( {}, '', removeParameterFromUrl( url, 'ac-segment' ) );
	}

	checkButtonStatus() {
		let button = document.querySelector( '.ac-segments__create .button' );
		let enabled = true;
		let state = this.getCurrentState();
		let rules = JSON.parse( state.rules );

		if ( rules.rules.length === 0 ) {
			enabled = false;
		}

		if ( enabled ) {
			button.removeAttribute( 'disabled' );
		} else {
			button.setAttribute( 'disabled', 'disabled' );
		}
	}

	initEvents() {
		this.ToggleButton.events.on( 'open', () => {
			this.Container.show();
			this.Container.moveToElement( this.ToggleButton.el );
		} );

		this.ToggleButton.events.on( 'close', () => {
			this.Container.hide();
		} );

		this.Container.el.addEventListener( 'click', e => e.stopPropagation() );
		document.addEventListener( 'click', () => {
			if ( this.ToggleButton.isOpen() )
				this.ToggleButton.triggerClose();
		} );

		this.Modal.events.on( 'save', ( data ) => {
			this.Modal.setLoading();
			saveSegment( data.name, JSON.stringify( this.Search.getRules() ), this.Sorting.orderby, this.Sorting.order )
				.done( response => {
					this.Modal.setLoading( false );
					if ( response.success ) {
						Segments.pushState( response.data.segment );
						this.Container.addSegment( response.data.segment );
						this.Container.setCurrent( response.data.segment.name );
						this.Modal.finish();
					} else {
						this.Modal.displayError( response.data.error );
					}
				} );
		} );

		new Instructions( '.ac-segments__instructions', '#ac-segments-instructions' );
	}

	static pushState( segment ) {
		history.replaceState( {}, segment.name, segment.url );
	}

}