import {toPixel} from "../../helpers/strings"
import {getOffset} from "../../helpers/document"
import {deleteSegment, retrieveSegments} from "./ajax";

export default class SegmentContainer {
	constructor( el ) {
		this.el = el;
		this.current = null;

		this.addNewButton = el.querySelector( '.ac-segments__create button' );
		this.init();
		this.refreshSegments();
	}

	init() {
		this.addNewButton.addEventListener( 'click', () => {
			AdminColumns.Modals.get( 'create_segment' ).open();
		} );
	}

	show() {
		this.el.classList.add( '-active' );
	}

	hide() {
		this.el.classList.remove( '-active' );
	}

	moveToElement( el ) {
		let offset = getOffset( el );

		this.el.style.left = toPixel( offset.left + Math.round( el.offsetWidth / 2 ) );
		this.el.style.top = toPixel( offset.top );
	}

	removeSegment( name ) {
		let row = this.el.querySelector( `.ac-segments__list [data-name="${name}"]` );

		if ( row ) {
			row.remove();
		}
	}

	addSegment( segment ) {
		let element = document.createElement( "div" );

		element.classList.add( 'ac-segment' );
		element.dataset.name = segment.name;
		element.innerHTML = `<a class="ac-segment__label" href="${segment.url}">${segment.name}</a>
						<button class="ac-segment__delete"><span class="dashicons dashicons-no-alt"></span></button>`;

		this.el.querySelector( '.ac-segments__list' ).insertAdjacentElement( 'beforeend', element );
		this.initSegmentEvents( element );

		return element;
	}

	setCurrent( label ) {
		this.current = label;

		let segments = document.querySelectorAll( '.ac-segments__list .ac-segment' );

		for ( let i = 0; i < segments.length; i++ ) {
			let segment = segments[ i ];

			segment.classList.remove( '-current' );

			if ( segment.dataset.name === this.current ) {
				segment.classList.add( '-current' );
			}
		}
	}

	initSegmentEvents( segment ) {
		let name = segment.dataset.name;

		segment.querySelector( '.ac-segment__delete' ).addEventListener( 'click', e => {
			e.preventDefault();

			deleteSegment( name ).done( response => {
				if ( response.success ) {
					this.removeSegment( name );
				}
			} );

		} );
	}

	refreshSegments() {
		retrieveSegments().done( response => {
			response.data.forEach( segment => {
				this.addSegment( segment );
			} );
			this.setCurrent( this.el.dataset.initial );
		} );
	}

}
