export default class Instructions {
	constructor( element_selector, info_selector ) {
		// create pointer
		let el = jQuery( element_selector );
		let info = jQuery( info_selector );
		let timeout = null;

		el.pointer( {
			content : info.html(),
			position : {
				edge : 'left',
				align : 'middle'
			},
			pointerClass : 'ac-wp-pointer wp-pointer wp-pointer-left noclick -nodismiss',
			pointerWidth : 250,
		} );

		el.hover( function() {
			el.pointer( 'open' );
			clearTimeout( timeout );
		}, function() {
			timeout = setTimeout( () => {
				el.pointer( 'close' );
			}, 500 );
		} );
	}
}