const action = 'acp_search_segment_request';

const deleteSegment = ( name ) => {
	return jQuery.ajax( {
		url : ajaxurl,
		method : 'post',
		data : {
			_ajax_nonce : AC.ajax_nonce,
			action : action,
			list_screen : AC.list_screen,
			layout : AC.layout,
			name : name,
			method : 'delete',
		}
	} );
};

const retrieveSegments = () => {
	return jQuery.ajax( {
		url : ajaxurl,
		method : 'post',
		data : {
			_ajax_nonce : AC.ajax_nonce,
			action : action,
			list_screen : AC.list_screen,
			layout : AC.layout,
			name : name,
			method : 'read',
		}
	} );
};

const saveSegment = ( name, rules, orderby, order ) => {
	return jQuery.ajax( {
		url : ajaxurl,
		method : 'post',
		data : {
			_ajax_nonce : AC.ajax_nonce,
			action : action,
			list_screen : AC.list_screen,
			layout : AC.layout,
			name : name,
			method : 'create',
			rules : rules,
			order : order,
			orderby : orderby
		}
	} );
};

const saveCurrent = ( name ) => {
	return jQuery.ajax( {
		url : ajaxurl,
		method : 'post',
		data : {
			_ajax_nonce : AC.ajax_nonce,
			action : action,
			list_screen : AC.list_screen,
			layout : AC.layout,
			name : name,
			method : 'create',
			rules : JSON.stringify( AdminColumns.Search.getRules() ),
			order : this.Sorting.order,
			orderby : this.Sorting.orderby
		}
	} );
};

export {deleteSegment, retrieveSegments, saveSegment}